.login__button {
  font-family: Inter;
  width: 358px;
  height: 50px;
  background: #ffffff;
  border-radius: 2px;
  font-size: 18px;
  line-height: 22px;
  transition: all 0.5s;
  padding: 0;
  margin-bottom: 12px;
}

.login__button:hover {
  opacity: 0.85;
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .login__button {
    width: 260px;
    height: 46px;
    font-size: 16px;
  }
}
